import { login } from '../../utils/request.js'
import headerBar from '../../components/header-bar.vue'
let socketTask = null
export default {
	data() {
		return {
			usernameFoucus: false,
			passwordFoucus: false,
			username: '',
			password: '',
		}
	},
	components: {
		headerBar
	},
	onLoad() {
		
	},
	methods: {
		register() {
			uni.navigateTo({ url: '../register/register' })
		},
		findPassword() {
			uni.navigateTo({ url: '../find-password/find-password' })
		},
		loginSubmit() {
			if (!this.username) {
				uni.showToast({
					title: '请输入账号',
					icon: 'none'
				})
				return
			}
			if (!this.password) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none'
				})
				return
			}
			let option = {
				username: this.username,
				password: this.password
			}
			login('/login', option).then(res => {
				if (res.code === 0) {
					uni.showToast({ icon: 'loading', title: '登录成功', duration:1000 , mask: true})
					uni.setStorageSync('token', res.data.token)
					uni.setStorageSync('username', res.data.username)
					let timer = setTimeout(() => {
						clearTimeout(timer)
						uni.reLaunch({ url: '../index/index' })
					}, 1000)
				} else {
					uni.showToast({ icon: 'none', title: res.msg })
				}
			})
		}
	}
}
